.widget {
  margin-top: 14px;
    display: flex;
    justify-content: space-between;
    flex: 1;
    padding: 10px;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
    border-radius: 5px;
    height: 45px;
  
    .left,
    .right {
      display: flex;
      flex-direction: column;
      justify-content: center;
  
      .title {
        font-weight: bold;
        font-size: 8px;
        color: rgb(160, 160, 160);
      }
  
      .counter {
        font-size: 14px;
        font-weight: 600;
      }
  
  
      .icon {
        font-size: 30px;
        padding: 5px;
        border-radius: 5px;
        align-self: flex-end;
      }
    }
  }