.slider-image-bar {
  padding: 7px 110px 6px 30px;
  margin-top: 10px;
  color: #fff;
  background: linear-gradient(
    90deg,
    rgba(117, 95, 222, 1) 25%,
    rgba(255, 255, 255, 1) 100%
  );
  font-weight: 700;
  .icon-style {
    background: #8554e8;
    width: 37px;
    height: 35px;
    border-radius: 8px;
    cursor: pointer;
  }
}
